.container{
  padding: 0%;
  margin: 0%;
  background:linear-gradient(to bottom ,blue,skyblue);
  overflow-y: hidden;
}
.container .count{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content:space-around;
  align-items: center;
  overflow: wrap;
}

.container .count .button{
  width: 100px;
  height: 100px;
  cursor: pointer;
   font-size: 80px;
   color: aliceblue;
   background-color: rgb(54, 73, 243);
   border-radius: 50%;
   border:1px solid rgb(185, 185, 185);
}
.count p{
  font-size: 250px;
  color: white;
}
 .btn2{
   position: relative;
   top: -90px;
   left: 700px;
   font-size: 20px;
   color: white;
   background-color: rgb(48, 69, 230);
   border: 0.5px solid white;
   padding: 5px 20px;
   cursor: pointer;
   border-radius: 20px;
   letter-spacing:1;
} 
.container .iconss{
  font-size: 30px;
  color: white;
  display: flex;
  justify-content:space-evenly;
  align-items: center;
  margin-top: 20px;
  
}